.product
{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
    gap:50px;
}

.card
{
   
    padding:10px;
    background-color:white;
    box-shadow:0 0 50px rgba(0,0,0,0.2)

}
.card img
{
    height:200px;
    width:200px;
    
}

.card h1
{
  font-size:18px;
  margin:10px;
}
.card h2
{
    font-size:16px;
    color:blue;
}
.btn
{
    background-color: blue;
    color:white;
}
.btn:hover
{
    background-color:red;
    color:white;
}
img
{
    width:100%;
    height:400px;
}
.h1
{
    position: relative;
    bottom:380px;
    color:blue;
}
p
{
    font-size: 25px;
    color:black;
}